.team__name { margin-top: 12px; font-family: "Arbutus Slab", Helvetica, Arial, sans-serif; font-size: 15px; color: #3F566E; letter-spacing: 0; }

.head__label { padding-bottom: 3px; font-weight: 300; font-family: "Montserrat", Helvetica, Arial, sans-serif; font-size: 20px; color: #333D47; letter-spacing: 0.57px; line-height: 0.7; text-transform: uppercase; }

@media only screen and (max-width: 767px) { .head__label { padding-bottom: 5px; font-size: 16px; line-height: 1.2; } }

.head__title { font-family: "Montserrat", Helvetica, Arial, sans-serif; font-weight: 700; font-size: 62px; color: #FFDC45; letter-spacing: 1.77px; line-height: 0.9; text-transform: uppercase; }

@media only screen and (max-width: 767px) { .head__title { font-size: 48px; } }

.head__title_pink { color: #FF539E; }

.head__title_azure { color: #63F6EB; }

.partners { padding-bottom: 131px; }

@media only screen and (max-width: 767px) { .partners { padding-bottom: 80px; } }

.partners__logos { font-size: 0; text-align: center; }

.partners__logos img { max-width: 100%; }

.footer { padding: 54px 0; color: #fff; background-image: linear-gradient(229deg, #36424D 3%, #2B333C 100%); border-bottom: 5px solid #000; }

.footer__wrap { width: 100%; display: table; }

@media only screen and (max-width: 767px) { .footer__wrap { display: block; } }

.footer__contacts, .footer__button { vertical-align: middle; display: table-cell; }

@media only screen and (max-width: 767px) { .footer__contacts, .footer__button { display: block; } }

.footer__button { text-align: right; }

@media only screen and (max-width: 767px) { .footer__button { margin-top: 20px; text-align: center; } }

.footer__info { vertical-align: top; display: inline-block; }

@media only screen and (max-width: 767px) { .footer__info { display: block; padding-bottom: 22px; } }

.footer__info:not(:last-child) { margin-right: 50px; }

@media only screen and (max-width: 767px) { .footer__info:not(:last-child) { margin: 0; } }

.footer__icon { margin-bottom: 7px; }

.footer__subtitle { font-family: "Montserrat", Helvetica, Arial, sans-serif; font-size: 16px; font-weight: 700; line-height: 1.4; letter-spacing: 0.46px; }

.footer__meta { font-size: 15px; font-weight: 400; line-height: 20px; letter-spacing: -0.02px; }

.footer__meta a { color: inherit; transition: color 0.25s; }

.no-touch .footer__meta a:hover { color: #FFDC45; }

.footer__subnav { margin: 69px -13px 0; }

@media only screen and (max-width: 1023px) { .footer__subnav { margin-top: 13px; } }
